import React from "react";

const IconMinus = () => {
  return (
    <div className="module__icon">
      <svg
        width="20"
        height="2"
        viewBox="0 0 20 2"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <rect width="20" height="2" rx="1" fill="white" />
      </svg>
    </div>
  );
};

export default IconMinus;
